.calcPayment {
  width: 90%;
  margin-top: 50px;
  background: #0066cc;
  border-radius: 6px;
  color: #fff;
  padding: 30px;
}
.calcPayment .paymentAll {
  font-size: 34px;
}
.calcPayment .calcPaymentContent {
  display: flex;
}
.calcPayment .calcPaymentContent div {
  margin-right: 30px;
}
.calcPayment .calcPaymentContent p {
  font-size: 14px;
  margin-bottom: 5px;
}
.calcPayment .calcPaymentContent span {
  font-size: 20px;
}
.calcPayment .PaymentBtn {
  display: flex;
  margin-top: 20px;
  flex-wrap: wrap;
}
.calcPayment .PaymentBtn div, .calcPayment .PaymentBtn button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #ffd200;
  color: #000;
  padding: 0 20px;
  border-radius: 4px;
  height: 46px;
  display: flex;
  align-items: center;
  margin-right: 20px;
  min-width: 350px;
}
.calcPayment .PaymentBtn div:hover, .calcPayment .PaymentBtn button:hover {
  cursor: pointer;
  border-color: #ffe400;
  background-color: #ffe400;
}
.calcPayment .PaymentBtn div img, .calcPayment .PaymentBtn button img {
  width: 18px;
  margin-right: 10px;
}
.calcPayment .PaymentBtn div span, .calcPayment .PaymentBtn button span {
  font-size: 15px;
}
.calcPayment .userForm input {
  height: 50px;
  width: 80%;
  margin-top: 20px;
}